import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import { IonIcon } from '@ionic/react';
import { logoGithub, logoLinkedin, mailOutline } from 'ionicons/icons';

export default function Profile() {
    return (
      <Container fluid className="my-3">
        <Row className="align-items-center">
          <Col xs={4}>
            <Image rounded fluid
                src="/TurkeyCat.jpg"
            />
          </Col>
          <Col>
            <p className="mb-4">Hi, I'm Alex. I'm a Software Engineer living in Chicago. My work experience is backend heavy with lots of web development with Django.</p>
            <h4>Work Experience:</h4>
            <ul>
                <li><strong>Lead Backend Engineer</strong> at Steelseries (April 2024 - March 2025)</li>
                <li><strong>Senior Backend Engineer</strong> at Steelseries (August 2020 - April 2024)</li>
                <li><strong>Full Stack Developer</strong> at National Association of Realtors (September 2017 - October 2018)</li>
                <li><strong>Software Developer</strong> at Northshore Care Supply (November 2014 - September 2017)</li>
            </ul>
          </Col>
        </Row>
      </Container>
    );
  }