import { Container, Row, Col, Image } from 'react-bootstrap';
import { IonIcon } from '@ionic/react';
import { logoGithub, logoLinkedin, mailOutline } from 'ionicons/icons';

export default function Profile() {
    return (
      <Container fluid className="my-3">
        <Row>
          <Col xs={6}>
          <Image rounded fluid
            src="/TombOfStJohn.jpg"
            alt="Tomb of St. John the Apostle"
            height="400px"
            width="auto"
          />
          </Col>
          <Col>
            <h1 className="font-bold my-5">Alex Mourousias</h1>
            <h4>
              <IonIcon icon={mailOutline} size="large" className="mx-3 align-bottom" ></IonIcon>
              <a href="mailto:alex.mourousias@gmail.com">alex.mourousias@gmail.com</a>
            </h4>
            <h4 class="my-3">
              <IonIcon icon={logoGithub} size="large" className="mx-3 align-bottom" ></IonIcon>
              <a href="https://github.com/amourousias">@amourousias</a>
            </h4>
            <h4>
              <IonIcon icon={logoLinkedin} size="large" className="mx-3 align-bottom" ></IonIcon>
              <a href="https://www.linkedin.com/in/alexander-mourousias-07315463/">@alexander-mourousias</a>
            </h4>
          </Col>
        </Row>
      </Container>
    );
  }